// =============================================================================
// STARTUP ENVIRONMENT VALIDATION
// =============================================================================
// Validates critical environment variables at application startup.
// Fails fast in production if configuration is invalid or dangerous.
//
// This module prevents:
// - Missing critical configuration
// - Localhost fallbacks in production
// - Test/dummy facilitators in production
// - Unconfigured settlement in production

import { createLogger } from '@/lib/logger';

const logger = createLogger({ component: 'StartupValidation' });

// =============================================================================
// TYPES
// =============================================================================

interface ValidationError {
  variable: string;
  message: string;
  severity: 'error' | 'warning';
}

interface ValidationResult {
  valid: boolean;
  errors: ValidationError[];
  warnings: ValidationError[];
}

// =============================================================================
// REQUIRED ENVIRONMENT VARIABLES
// =============================================================================

/**
 * Environment variables that MUST be set in production.
 */
const REQUIRED_IN_PRODUCTION = [
  { name: 'DATABASE_URL', description: 'PostgreSQL connection string' },
  { name: 'NEXT_PUBLIC_APP_URL', description: 'Public application URL' },
  { name: 'CRON_SECRET', description: 'Secret for cron job authentication' },
];

/**
 * Environment variables that SHOULD be set for full functionality.
 */
const RECOMMENDED_IN_PRODUCTION = [
  { name: 'GOVERNANCE_ADMIN_API_KEY', description: 'Admin API key for governance' },
  { name: 'UPSTASH_REDIS_REST_URL', description: 'Redis URL for caching' },
  { name: 'UPSTASH_REDIS_REST_TOKEN', description: 'Redis authentication token' },
];

/**
 * Settlement-related variables required for on-chain operations.
 */
const SETTLEMENT_VARS = [
  { name: 'ATOMIC_BATCH_SETTLEMENT_ADDRESS', description: 'Settlement contract address' },
  { name: 'NEXFLOW_TREASURY_ADDRESS', description: 'Treasury wallet address' },
];

// =============================================================================
// VALIDATION FUNCTIONS
// =============================================================================

/**
 * Validate that a URL is not localhost.
 */
function isLocalhostUrl(url: string): boolean {
  const lower = url.toLowerCase();
  return (
    lower.includes('localhost') ||
    lower.includes('127.0.0.1') ||
    lower.includes('0.0.0.0')
  );
}

/**
 * Validate that an address is not the zero address.
 */
function isZeroAddress(address: string): boolean {
  return (
    !address ||
    address === '0x0000000000000000000000000000000000000000' ||
    address === '0x0'
  );
}

/**
 * Validate environment for production deployment.
 * Throws an error if critical issues are found.
 */
export function validateEnvForProduction(): ValidationResult {
  const errors: ValidationError[] = [];
  const warnings: ValidationError[] = [];
  const isProduction = process.env.NODE_ENV === 'production';

  if (!isProduction) {
    logger.debug('Skipping production validation (NODE_ENV !== production)');
    return { valid: true, errors: [], warnings: [] };
  }

  logger.info('Running production environment validation...');

  // Check required variables
  for (const { name, description } of REQUIRED_IN_PRODUCTION) {
    const value = process.env[name];
    if (!value || value.trim() === '') {
      errors.push({
        variable: name,
        message: `Required variable ${name} is not set. Purpose: ${description}`,
        severity: 'error',
      });
    }
  }

  // Check NEXT_PUBLIC_APP_URL is not localhost
  const appUrl = process.env.NEXT_PUBLIC_APP_URL;
  if (appUrl && isLocalhostUrl(appUrl)) {
    errors.push({
      variable: 'NEXT_PUBLIC_APP_URL',
      message: 'NEXT_PUBLIC_APP_URL contains localhost, which is invalid in production',
      severity: 'error',
    });
  }

  // Check DATABASE_URL is not localhost (unless explicitly allowed for testing)
  const dbUrl = process.env.DATABASE_URL;
  if (dbUrl && isLocalhostUrl(dbUrl) && process.env.ALLOW_LOCAL_DB !== 'true') {
    errors.push({
      variable: 'DATABASE_URL',
      message: 'DATABASE_URL contains localhost, which is invalid in production',
      severity: 'error',
    });
  }

  // Check dummy facilitator is not enabled
  if (process.env.ENABLE_DUMMY_FACILITATOR === 'true') {
    errors.push({
      variable: 'ENABLE_DUMMY_FACILITATOR',
      message: 'ENABLE_DUMMY_FACILITATOR=true is not allowed in production',
      severity: 'error',
    });
  }

  // Check mock settlement is not enabled
  if (process.env.ALLOW_MOCK_SETTLEMENT === 'true') {
    warnings.push({
      variable: 'ALLOW_MOCK_SETTLEMENT',
      message: 'ALLOW_MOCK_SETTLEMENT=true allows mock tx hashes. Disable for real money.',
      severity: 'warning',
    });
  }

  // Check recommended variables
  for (const { name, description } of RECOMMENDED_IN_PRODUCTION) {
    const value = process.env[name];
    if (!value || value.trim() === '') {
      warnings.push({
        variable: name,
        message: `Recommended variable ${name} is not set. Purpose: ${description}`,
        severity: 'warning',
      });
    }
  }

  // Check settlement configuration
  const settlementEnabled = process.env.SETTLEMENT_EXECUTOR_ENABLED === 'true';
  if (settlementEnabled) {
    for (const { name, description } of SETTLEMENT_VARS) {
      const value = process.env[name];
      if (!value || value.trim() === '') {
        errors.push({
          variable: name,
          message: `Settlement is enabled but ${name} is not set. Purpose: ${description}`,
          severity: 'error',
        });
      } else if (name.includes('ADDRESS') && isZeroAddress(value)) {
        errors.push({
          variable: name,
          message: `${name} is set to the zero address, which is invalid`,
          severity: 'error',
        });
      }
    }
  } else {
    logger.warn(
      'SETTLEMENT_EXECUTOR_ENABLED is not true. On-chain settlement requests will return 503.'
    );
  }

  // Log results
  if (errors.length > 0) {
    logger.error({ errors }, 'Production environment validation FAILED');
  }
  if (warnings.length > 0) {
    logger.warn({ warnings }, 'Production environment validation warnings');
  }
  if (errors.length === 0 && warnings.length === 0) {
    logger.info('Production environment validation PASSED');
  }

  return {
    valid: errors.length === 0,
    errors,
    warnings,
  };
}

/**
 * Run validation and throw if critical errors found.
 * Call this at application startup.
 */
export function validateEnvOrThrow(): void {
  const result = validateEnvForProduction();

  if (!result.valid) {
    const errorMessages = result.errors.map((e) => `  - ${e.variable}: ${e.message}`).join('\n');
    throw new Error(
      `Production environment validation failed:\n${errorMessages}\n\nFix these issues before deploying to production.`
    );
  }
}

/**
 * Get the base URL for internal API calls.
 * In production, requires NEXT_PUBLIC_APP_URL to be set.
 * In development, falls back to localhost.
 */
export function getBaseUrl(): string {
  const isProduction = process.env.NODE_ENV === 'production';
  const appUrl = process.env.NEXT_PUBLIC_APP_URL;

  if (isProduction) {
    if (!appUrl) {
      throw new Error('NEXT_PUBLIC_APP_URL must be set in production');
    }
    return appUrl;
  }

  // Development fallback
  return appUrl || 'http://localhost:3000';
}

// =============================================================================
// SINGLETON VALIDATION STATE
// =============================================================================

let hasValidated = false;

/**
 * Run validation once at startup.
 * Safe to call multiple times; only runs once.
 */
export function runStartupValidation(): void {
  if (hasValidated) return;
  hasValidated = true;

  try {
    validateEnvOrThrow();
  } catch (error) {
    // In production, this should crash the app
    if (process.env.NODE_ENV === 'production') {
      console.error('[FATAL] Startup validation failed:', error);
      process.exit(1);
    } else {
      // In development, just log the warning
      console.warn('[WARN] Startup validation issues (non-fatal in development):', error);
    }
  }
}


